const getRandomPrediction = (predictions: string[]) => {
  return predictions[Math.floor(Math.random() * predictions.length)]
}

const getSunSign = (birthDate: string): string => {
  const date = new Date(birthDate)
  const month = date.getMonth() + 1
  const day = date.getDate()

  if ((month === 3 && day >= 21) || (month === 4 && day <= 19)) return 'Áries'
  if ((month === 4 && day >= 20) || (month === 5 && day <= 20)) return 'Touro'
  if ((month === 5 && day >= 21) || (month === 6 && day <= 20)) return 'Gêmeos'
  if ((month === 6 && day >= 21) || (month === 7 && day <= 22)) return 'Câncer'
  if ((month === 7 && day >= 23) || (month === 8 && day <= 22)) return 'Leão'
  if ((month === 8 && day >= 23) || (month === 9 && day <= 22)) return 'Virgem'
  if ((month === 9 && day >= 23) || (month === 10 && day <= 22)) return 'Libra'
  if ((month === 10 && day >= 23) || (month === 11 && day <= 21)) return 'Escorpião'
  if ((month === 11 && day >= 22) || (month === 12 && day <= 21)) return 'Sagitário'
  if ((month === 12 && day >= 22) || (month === 1 && day <= 19)) return 'Capricórnio'
  if ((month === 1 && day >= 20) || (month === 2 && day <= 18)) return 'Aquário'
  return 'Peixes'
}

const getLuckyColorForSign = (sign: string): string => {
  const signColors: {[key: string]: string} = {
    'Áries': 'Vermelho',
    'Touro': 'Verde',
    'Gêmeos': 'Amarelo',
    'Câncer': 'Prateado',
    'Leão': 'Dourado',
    'Virgem': 'Marrom',
    'Libra': 'Rosa',
    'Escorpião': 'Bordô',
    'Sagitário': 'Roxo',
    'Capricórnio': 'Preto',
    'Aquário': 'Azul Turquesa',
    'Peixes': 'Azul Marinho'
  }
  return signColors[sign] || 'Dourado'
}

const generateHoroscope = (birthDate: string): HoroscopeData => {
  const sign = getSunSign(birthDate)
  const today = new Date().toLocaleDateString('pt-BR')
  
  // Para signos que têm estrutura completa
  if (sign === 'Áries' || sign === 'Touro' || sign === 'Gêmeos' || sign === 'Câncer' || sign === 'Leão' || sign === 'Virgem') {
    const signData = horoscopePredictions[sign as keyof typeof horoscopePredictions]
    if (signData && typeof signData === 'object' && 'general' in signData) {
      return {
        sign,
        date: today,
        generalPrediction: getRandomPrediction(signData.general),
        love: getRandomPrediction(signData.love),
        career: getRandomPrediction(signData.career),
        health: getRandomPrediction(signData.health),
        luckyNumbers: [Math.floor(Math.random() * 9) + 1, Math.floor(Math.random() * 9) + 1, Math.floor(Math.random() * 9) + 1],
        luckyColors: ['Dourado', 'Azul', 'Verde'],
        advice: 'Confie em sua intuição e aproveite as oportunidades que se apresentam.',
        mood: 'Otimista',
        compatibility: 'Signos compatíveis',
        challenges: ['Desafios menores'],
        opportunities: ['Novos projetos', 'Conexões sociais', 'Crescimento pessoal']
      }
    }
  }
  
  // Para outros signos (estrutura simplificada)
  const signPredictions = horoscopePredictions[sign as keyof typeof horoscopePredictions]
  const predictions = Array.isArray(signPredictions) ? signPredictions : ['Dia promissor com novas oportunidades.']
  
  return {
    sign,
    date: today,
    generalPrediction: getRandomPrediction(predictions),
    love: 'Momento favorável para relacionamentos e conexões especiais.',
    career: 'Oportunidades profissionais se apresentam com força.',
    health: 'Energia equilibrada favorece bem-estar geral.',
    luckyNumbers: [Math.floor(Math.random() * 9) + 1, Math.floor(Math.random() * 9) + 1, Math.floor(Math.random() * 9) + 1],
    luckyColors: ['Dourado', 'Azul', 'Verde'],
    advice: 'Mantenha-se positivo e aproveite as energias favoráveis.',
    mood: 'Equilibrado',
    compatibility: 'Signos compatíveis',
    challenges: ['Desafios menores'],
    opportunities: ['Novas possibilidades', 'Crescimento']
  }
}

